"use client";

import { useEffect, useRef } from "react";
import { Chart } from "chart.js";
import { CandlestickController, CandlestickElement, OhlcController, OhlcElement } from "chartjs-chart-financial";
import 'chartjs-adapter-date-fns';

Chart.register(CandlestickController, CandlestickElement, OhlcController, OhlcElement);

const CandlestickChart = ({ ohlcData, currencyPair }) => {
  const canvasRef = useRef(null);
  const chartRef = useRef(null);

  useEffect(() => {
    if (ohlcData.length === 0 || !canvasRef.current) return;

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;

    const chartConfig = {
      type: 'candlestick',
      data: {
        datasets: [{
          label: currencyPair,
          data: ohlcData,
        }]
      },
      options: {
        responsive: true,
        scales: {
          x: {
            type: 'time',
            time: {
              unit: 'minute',
            },
            title: {
              display: true,
              text: 'Time',
            },
          },
          y: {
            title: {
              display: true,
              text: 'Price (USD)',
            },
          },
        },
      },
    };

    if (chartRef.current) {
      chartRef.current.destroy();
    }

    chartRef.current = new Chart(ctx, chartConfig);

    return () => {
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, [ohlcData, currencyPair]);

  return (
    <div>
      <h3>{currencyPair} Candlestick Chart</h3>
      <canvas ref={canvasRef}></canvas>
    </div>
  );
};

export default CandlestickChart;